/**
 * dialogify
 * https://github.com/OneupNetwork/dialogify
 */

import dialogPolyfill from './dialog-polyfill.esm';

(function ($, window, undefined) {
    'use strict';

    // global config
    let config = window.dialogifyConfig;
    if (config == null || typeof config != 'object') {
        config = {};
    }

    // default global config
    config.locale = config.locale || 'zh_TW';

    class Dialogify {

        constructor(source, options) {
            let dialog = window.document.createElement('dialog');
            dialogPolyfill.registerDialog(dialog);

            if (options == null || typeof options != 'object') {
                options = {};
            }

            this.options = options;

            let ajaxPrefix = options.ajaxPrefix === undefined ? '/ajax/' : options.ajaxPrefix;

            let self = this;
            let content;
            let ajax = false;
            if (source.charAt(0) == '#') {
                content = $(source).html();
            } else if (source.indexOf(ajaxPrefix) == 0) {
                let loadingSvg = 'data:image/svg+xml;base64,';
                content = `<div class="dialogify-ajax-content"><img class="dialogify-ajax-loading" src="${loadingSvg}"></div>`;
                ajax = true;
            } else {
                content = source;
            }

            content = `<div class="dialogify__body">${content}</div>`;

            let widthClass = options.size || 'dialogify__fixedwidth';
            let dialogClass = 'dialogify';
            if (options.fixed !== false) {
                dialogClass += ' fixed';
            }

            let dialogHtml = `<div class="dialogify__content ${widthClass}"><div></div></div>`;

            if (options.useDialogForm !== false) {
                let edgeSubmitIssue = /(?:edge|iPad)/i.test(window.navigator.userAgent) ? ' onsubmit="return false;"' : '';
                dialogHtml = `<form method="dialog"${edgeSubmitIssue}>${dialogHtml}</form>`;
            }

            this.id = `dialogify_${++Dialogify.counter}`;

            // create dialog element
            $(dialog).attr('id', this.id)
                .addClass(dialogClass)
                .append(dialogHtml)
                .on('close', function (e) {
                    $(self).triggerHandler('close');
                    $(this).remove();
                    if (options.scroll === false) {
                        $('body').css({'overflow': '', 'padding-right': ''});
                        $('.dialog-mask').remove();
                    }
                })
                .on('cancel', function (e) {
                    $(self).triggerHandler('cancel');
                    if (options.closable === false) {
                        e.preventDefault();
                    }
                })
                .click(function (e) {
                    if (options.closable !== false && e.target == dialog) {
                        $(self).triggerHandler('cancel');
                        dialog.close();
                    }
                })
                .appendTo('body');

            // close button
            if (options.closable !== false) {
                let closeButtonImage = 'data:image/svg+xml;base64,';
                let closeButtonClassName = '';
                let closeButtonStyle = {};

                let closeButton = options.closeButton || config.closeButton;
                if (closeButton != null && typeof closeButton == 'object') {
                    closeButtonImage = closeButton.image || closeButtonImage;
                    closeButtonClassName = closeButton.className || closeButtonClassName;
                    closeButtonStyle = closeButton.style || closeButtonStyle;
                }

                let $closeImage = $('<img>').attr('src', closeButtonImage);
                let $closeButton = $('<a>').addClass('dialogify__close')
                    .css(closeButtonStyle)
                    .append($closeImage)
                    .click(function (e) {
                        $(dialog).triggerHandler('cancel');
                        dialog.close();
                    });

                if (closeButtonClassName) {
                    $closeButton.addClass(closeButtonClassName);
                }

                $(dialog).append($closeButton);
            }

            // custom style
            let dialogConfig = options.dialog || config.dialog;
            if (dialogConfig != null && typeof dialogConfig == 'object') {
                $(dialog).css(dialogConfig.style || {});
                if (dialogConfig.className) {
                    $(dialog).addClass(dialogConfig.className);
                }

                let $dialogContent = $(dialog).find('.dialogify__content');
                $dialogContent.css(dialogConfig.contentStyle || {});
                if (dialogConfig.contentClassName) {
                    $dialogContent.addClass(dialogConfig.contentClassName);
                }
            }

            // append content
            this.$content = $(dialog).find('.dialogify__content > div');
            this.$content.append(content);

            // ajax content
            if (ajax) {
                $.get(source, options.ajaxData || {}, function (resp) {
                    if (options.ajaxComplete) {
                        options.ajaxComplete.call(self);
                    }

                    self.$content.find('.dialogify-ajax-content').html(resp);
                }, 'html');
            }

            // public methods
            this.showModal = function () {
                if (options.scroll === false) {
                    preventScroll();
                }
                dialog.showModal();
                $(this).triggerHandler('show');
            };

            this.show = function () {
                if (options.scroll === false) {
                    preventScroll();
                }
                dialog.show();
                $(this).triggerHandler('show');
            };

            this.close = function () {
                dialog.close();
            };

            this.isOpen = function () {
                return dialog.open;
            };

            this.on = function (event, handler) {
                $(this).on(event, handler);
                return this;
            };

            // fix blurry render in some browser
            // require https://github.com/marcj/css-element-queries/blob/master/src/ResizeSensor.js
            // see also https://stackoverflow.com/a/42256897/3188956
            if (window.ResizeSensor) {
                new ResizeSensor(dialog, function () {
                    roundCssTransformMatrix(dialog);
                });
            }
        }

        // set title
        title(title) {
            let $titleBox = $('<h5>').addClass('dialogify_title')
                .append('<img src="data:image/svg+xml;base64,">')
                .append(title);

            if (this.$content.find('.dialogify_title').length) {
                this.$content.find('.dialogify_title').replaceWith($titleBox);
            } else {
                this.$content.prepend($titleBox);
            }

            return this;
        }

        // set buttons
        buttons(buttons, options) {
            if (!Array.isArray(buttons)) {
                buttons = [];
            }

            if (options == null || typeof options != 'object') {
                options = {};
            }

            this.$buttonList = {};

            let $buttonBox = $('<div>')
                .addClass('btn-box')
                .addClass(options.position || 'text-right');

            let self = this;
            for (let i = 0; i < buttons.length; i++) {
                if (typeof buttons[i] == 'string') {
                    let $btn = $(buttons[i]);
                    let btnId = $btn.attr('id') || i;
                    this.$buttonList[btnId] = $btn;
                    $buttonBox.append($btn);
                } else {
                    if (buttons[i] == null || typeof buttons[i] != 'object') {
                        buttons[i] = {};
                    }

                    let $btn = $('<button type="button">')
                        .addClass('btn btn-insert')
                        .addClass(buttons[i].type || '')
                        .data('click', buttons[i].click);

                    if (buttons[i].type == Dialogify.BUTTON_PRIMARY && this.options.useDialogForm !== false) {
                        $btn.attr('type', 'submit');
                    }

                    if (buttons[i].focused) {
                        $btn.prop('autofocus', true);
                    }

                    if (buttons[i].disabled) {
                        $btn.disable();
                    }

                    $btn.text(buttons[i].text || Dialogify.LOCALE[config.locale].close);
                    $btn.click(function (e) {
                        if (typeof $(this).data('click') == 'function') {
                            $(this).data('click').call(self, e);
                        }
                    });

                    this.$buttonList[buttons[i].id || i] = $btn;
                    $buttonBox.append($btn);
                }
            }

            if (this.$content.find('.btn-box').length) {
                this.$content.find('.btn-box').replaceWith($buttonBox);
            } else {
                this.$content.append($buttonBox);
            }

            return this;
        }

    };

    // constants
    Dialogify.counter = 0;

    Dialogify.SIZE_LARGE = 'dialogify__autowidth';

    Dialogify.BUTTON_CENTER = 'text-center';
    Dialogify.BUTTON_LEFT = 'text-left';

    Dialogify.BUTTON_PRIMARY = 'btn-primary';
    Dialogify.BUTTON_DANGER = 'btn-danger';

    Dialogify.LOCALE = {};
    Dialogify.LOCALE['zh_TW'] = { ok: '確定', cancel: '取消', close: '關閉' };
    Dialogify.LOCALE['zh_CN'] = { ok: '确定', cancel: '取消', close: '关闭' };
    Dialogify.LOCALE['en_US'] = { ok: 'Ok', cancel: 'Cancel', close: 'Close' };

    // static methods
    Dialogify.alert = function (message, options) {
        if (options == null || typeof options != 'object') {
            options = {};
        }

        if (options.dialogOptions == null || typeof options.dialogOptions != 'object') {
            options.dialogOptions = {};
        }

        return new Promise((resolve) => {
            new Dialogify(`<p>${message}</p>`, options.dialogOptions)
                .buttons([{
                    type: Dialogify.BUTTON_DANGER,
                    click: function (e) {
                        options.close && options.close.call(this);
                        resolve();
                        this.close();
                    }
                }], { position: Dialogify.BUTTON_CENTER })
                .on('cancel', function () {
                    options.close && options.close.call(this);
                    resolve();
                })
                .showModal();
        });
    };

    Dialogify.confirm = function (message, options) {
        if (options == null || typeof options != 'object') {
            options = {};
        }

        if (options.dialogOptions == null || typeof options.dialogOptions != 'object') {
            options.dialogOptions = {};
        }

        return new Promise((resolve) => {
            new Dialogify(`<p>${message}</p>`, options.dialogOptions)
                .buttons([
                    {
                        text: Dialogify.LOCALE[config.locale].cancel,
                        click: function (e) {
                            options.cancel && options.cancel.call(this);
                            resolve(false);
                            this.close();
                        }
                    },
                    {
                        text: Dialogify.LOCALE[config.locale].ok,
                        type: Dialogify.BUTTON_PRIMARY,
                        focused: true,
                        click: function (e) {
                            options.ok && options.ok.call(this);
                            resolve(true);
                            this.close();
                        }
                    }
                ])
                .on('cancel', function () {
                    options.cancel && options.cancel.call(this);
                    resolve(false);
                })
                .showModal();
        });
    };

    Dialogify.prompt = function (message, options) {
        if (options == null || typeof options != 'object') {
            options = {};
        }

        if (options.dialogOptions == null || typeof options.dialogOptions != 'object') {
            options.dialogOptions = {};
        }

        let placeholder = options.placeholder ? options.placeholder : '';
        let value = options.value ? options.value : '';
        let $input = $('<input>').attr('type', 'text').addClass('text-field dialogify-prompt-input').attr('placeholder', placeholder).attr('value', value);
        let $html = $('<div>').html(`<p>${message}</p>`);
        $html.append($input);

        return new Promise((resolve) => {
            new Dialogify($html.html(), options.dialogOptions)
                .buttons([
                    {
                        text: Dialogify.LOCALE[config.locale].cancel,
                        click: function (e) {
                            options.cancel && options.cancel.call(this);
                            resolve(null);
                            this.close();
                        }
                    },
                    {
                        text: Dialogify.LOCALE[config.locale].ok,
                        type: Dialogify.BUTTON_PRIMARY,
                        click: function (e) {
                            let value = this.$content.find('.dialogify-prompt-input').val();
                            options.ok && options.ok.call(this, value);
                            resolve(value);
                            this.close();
                        }
                    }
                ])
                .on('cancel', function () {
                    options.cancel && options.cancel.call(this);
                    resolve(null);
                })
                .showModal();
        });
    };

    Dialogify.closeAll = function () {
        $('dialog[id^=dialogify_]').each(function () {
            this.close();
        });
    };

    function preventScroll() {
        // 防止body滾動
        $('body').css({'overflow': 'hidden', 'padding-right': window.innerWidth - document.documentElement.clientWidth});

        // 避免部分overflow-y或overflow-x的設定導致背景可動
        var mask = document.createElement("div");
        $(mask).addClass('dialog-mask');
        $('body').append(mask);
    }

    function roundCssTransformMatrix(el) {
        el.style.transform = '';

        let mx = window.getComputedStyle(el, null);
        mx = mx.getPropertyValue('transform') ||
            mx.getPropertyValue('-webkit-transform') ||
            mx.getPropertyValue('-moz-transform') ||
            mx.getPropertyValue('-ms-transform') ||
            mx.getPropertyValue('-o-transform') || false;

        if (mx) {
            let values = mx.replace(/[ \(\)]|matrix/g, '').split(',');
            values[4] = Math.ceil(values[4]);
            values[5] = Math.ceil(values[5]);

            $(el).css('transform', `matrix(${values.join()})`);
        }
    }

    // inject css
    if (!$('#dialogifyCss').length) {
        $('<style>')
            .attr({ type: 'text/css', id: 'dialogifyCss' })
            .html('@charset "UTF-8";.dialogify{display:none;position:absolute;inset:50% auto auto 50%;transform:translate(-50%,-50%);margin:auto;max-width:1100px;min-width:300px;max-height:100vh;background-color:#fff;font-size:14px;color:#464646;border:none;border-radius:4px;overflow:auto;box-shadow:0 3px 4px #0000001a,0 1px 8px #0000000d,0 2px 20px #0000001a;z-index:2;padding:0;line-height:100%;font-family:Helvetica Neue,Helvetica,Roboto,Arial,Lucida Grande,PingFang TC,\\860b\\679c\\5137\\4e2d\\9ed1,\\5fae\\8edf\\6b63\\9ed1\\9ad4,Microsoft JhengHei,\\9ed1\\9ad4-\\7e41,sans-serif}.dialogify[open]{display:block}.dialogify.fixed{position:fixed;top:50%;left:50%;transform:translate(-50%,-50%)}.dialogify p{line-height:1.5}.dialogify>div{background-repeat:no-repeat;background-position:right bottom}.dialogify>div:after{content:"";position:absolute;bottom:0;left:0;width:20px;height:21px;transform:rotate(90deg);background-size:20px;background-repeat:no-repeat;opacity:.7}.dialogify>div hr{border:none;height:1px;background:#e0e6e8}.dialogify .dialogify__body{min-height:40px;zoom:1}.dialogify .dialogify__body:after{clear:both;content:".";display:block;width:0;height:0;visibility:hidden}.dialogify .dialogify__body a{color:#117e96}.dialogify .dialogify__body a:hover{color:#126e7d}.dialogify .dialogify__content{padding:22px 28px 20px 24px;box-sizing:border-box}.dialogify .dialogify__content-box{margin:20px auto}.dialogify .dialogify__maxheight{max-height:400px;overflow-y:scroll;overflow-x:hidden}.dialogify .dialogify__autowidth{max-width:1100px;min-width:560px}.dialogify .dialogify__fixedwidth{max-width:560px}.dialogify .dialog__maxheight{max-height:400px;overflow-y:auto}.dialogify .dialogify__close{position:absolute;top:8px;right:8px;color:#00555f}.dialogify .dialogify__close img{width:16px;height:16px;opacity:.85}.dialogify .dialogify__close:hover{cursor:pointer}.dialogify .dialogify__close:hover img{opacity:1}.dialogify img.dialogify-ajax-loading{position:absolute;left:50%;top:50%;width:24px;height:24px;transform:translate(-50%)}.dialogify a:focus{outline:none;-webkit-text-decoration:none;text-decoration:none}.dialogify .btn-danger-shadow{box-shadow:0 16px 26px -10px #f443368f,0 4px 25px #0000001f,0 8px 10px -5px #f4433633}.dialogify .btn-box{margin-top:20px;clear:both}.dialogify .btn-box .btn-insert{margin-left:8px;margin-right:3px}.dialogify .btn-box .check-group{float:left;margin-top:8px}.dialogify .btn-box .check-group input[type=checkbox]+label h6{font-size:13px;color:#a6a6a6}.dialogify .btn-box .check-group input[type=checkbox]+label a{color:#117e96}.dialogify .btn-box .check-group input[type=checkbox]+label a:hover{color:#126e7d;-webkit-text-decoration:none;text-decoration:none;transition:all .3s}.dialogify .btn-box .float-left,.dialogify .btn-box .float-left a{margin-left:0}.dialogify .btn-box .post__text-small{font-weight:400}.dialogify .btn-box .link-text{color:#117e96}.dialogify .link-form__actions{margin-top:10px;float:right;width:100%}.dialogify .btn{display:inline-block;border:1px solid transparent;border-radius:3px;padding:5px 12px;height:auto;font-size:14px;line-height:1.5;letter-spacing:1px;cursor:pointer;background-color:#e5e5e5;color:#a6a6a6;outline:none;-webkit-text-decoration:none;text-decoration:none}.dialogify .btn:hover{background:#dcdcdc;color:#a6a6a6}.dialogify .btn.is-disabled{opacity:.5;box-shadow:inset 0 1px 1px #0003;cursor:not-allowed!important}.dialogify .btn.is-disabled:hover{animation:.3s shake}@keyframes shake{0%{transform:rotate(1.5deg)}50%{transform:rotate(-2deg)}70%{transform:rotate(1.5deg)}to{transform:rotate(0)}}.dialogify .btn.btn-primary{background-color:#117e96;color:#fff}.dialogify .btn.btn-primary:hover{background-color:#126e7d}.dialogify .btn.btn-danger{background-color:#f44336;color:#fff}.dialogify .btn.btn-danger:hover{background:#de2427}.dialogify .btn.btn-danger.is-disabled:hover{background-color:#f44336}.dialogify h5.dialogify_title{font-size:18px;color:#00555f;line-height:1;margin:0 0 18px;font-family:Helvetica Neue,Helvetica,Roboto,Arial,Lucida Grande,PingFang TC,\\860b\\679c\\5137\\4e2d\\9ed1,Apple LiGothic Medium,\\5fae\\8edf\\6b63\\9ed1\\9ad4,sans-serif;font-weight:400}.dialogify h5.dialogify_title img{width:12px;height:10px;position:relative;top:-2px;margin-right:3px}.dialogify h5.dialogify_title .post__text-small img{width:12px;height:12px;top:-1px}.dialogify hr{background:#e0e6e8;border:none;height:1px;width:100%;margin-bottom:15px}.dialogify .text-right{text-align:right}.dialogify .text-center{text-align:center}.dialogify .text-left{text-align:left}.dialogify .text-field{margin:10px 0;border:1px solid #D9D9D9;border-radius:3px;padding:8px;box-sizing:border-box;width:100%;background-color:#fff;font-size:14px;color:#00555f}.dialogify .text-field:hover,.dialogify .text-field:focus{outline:none;border-color:#117e96}.dialogify .text-field::-webkit-input-placeholder,.dialogify .text-field:-moz-placeholder,.dialogify .text-field:-ms-input-placeholder,.dialogify .text-field::-moz-placeholder{color:#b3b3b3!important}.dialogify .text-field.is-error{border-color:#de2427;color:#de2427}.dialogify textarea.text-field{resize:none;min-height:100px;line-height:1.5;font-size:14px;overflow:auto}dialog::backdrop{background:#000;opacity:.7}dialog+.backdrop{position:fixed;inset:0;background:#000;opacity:.7}.dialog-mask{width:100vw;height:100vh;position:fixed;top:0;left:0;z-index:1;background-color:transparent}')
            .appendTo('head')
    }

    $.fn.extend({
        enable: function () {
            if ($(this).hasClass('btn btn-insert')) {
                $(this).prop('disabled', false).removeClass('is-disabled');
            }
        },
        disable: function () {
            if ($(this).hasClass('btn btn-insert')) {
                $(this).prop('disabled', true).addClass('is-disabled');
            }
        }
    });

    window.Dialogify = Dialogify;
})(jQuery, window);
